import React, { useState, useEffect, useCallback } from 'react';
import Layout from './layout'
import { Typography, Box, Link, IconButton, Collapse, Avatar, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import Pagination from '@material-ui/lab/Pagination';
import { KeyboardArrowUp, KeyboardArrowDown } from '@material-ui/icons';
import api from '../../api';
import { useDispatch } from 'react-redux';
import { actions } from '../../redux/_actions';

const useStyles = makeStyles(() => ({
  tableBox: {
    marginTop: 25
  },
  table: {
    width: '100%'
  },
  paginationWrapper: {
    marginTop: '10px',
    display: 'flex',
    justifyContent: 'center'
  }
}));

const useRowStyles = makeStyles({
  root: {
    '& > *': {
      borderBottom: 'unset',
    },
  },
  orderUser: {
    display: 'flex',
    alignItems: 'center',
    gap: '15px'
  },
  avatar: {
    color: 'white',
    backgroundColor: 'orangered'
  },
  productImage: {
    width: '40px',
    height: '40px',
    objectFit: 'contain'
  }
});

const OrderRow = (props) => {
  const { order } = props;
  const [open, setOpen] = React.useState(false);
  const classes = useRowStyles();

  const getAddressDetail = (address) => {
    let addr = '';
    try {
      addr = JSON.parse(address);
    } catch (ex) {
      console.log(ex);
    }
    return Object.keys(addr).map((key, idx) => (
      <TableRow key={idx}>
        <TableCell align="left">{key}</TableCell>
        <TableCell align="left">{addr[key]}</TableCell>
      </TableRow>
    ))
  }

  return (
    <>
      <TableRow className={classes.root}>
        <TableCell>
          <IconButton aria-label="expand row" size="small" onClick={() => setOpen(!open)}>
            {open ? <KeyboardArrowUp /> : <KeyboardArrowDown />}
          </IconButton>
        </TableCell>
        <TableCell align="left">
          <Box className={classes.orderUser}>
            <Avatar className={classes.avatar}>{order.user.email[0].toUpperCase()}</Avatar>
            <Typography>{order.user.email}</Typography>
          </Box>
        </TableCell>
        <TableCell align="center">$ {order.products.length * 14}</TableCell>
        <TableCell align="center">$ {order.totalFee}</TableCell>
        <TableCell align="center">$ {order.totalPrice}</TableCell>
        <TableCell align="center">{order.products.length}</TableCell>
      </TableRow>
      <TableRow>
        <TableCell style={{ paddingBottom: 0, paddingTop: 0 }} colSpan={6}>
          <Collapse in={open} timeout="auto" unmountOnExit>
            <Box margin={1} marginLeft='auto' maxWidth={400}>
              <Table size="small" aria-label="purchases">
                <TableBody>
                  <TableRow>
                    <TableCell align="left">Full Name</TableCell>
                    <TableCell align="left">{order.name}</TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell align="left">Phone</TableCell>
                    <TableCell align="left">{order.phone}</TableCell>
                  </TableRow>
                  {getAddressDetail(order.address)}
                </TableBody>
              </Table>
            </Box>
            <Box margin={1} marginLeft={10}>
              <Typography variant="h6" gutterBottom component="div">
                Items
              </Typography>
              <Table size="small" aria-label="purchases">
                <TableHead>
                  <TableRow>
                    <TableCell width={80}></TableCell>
                    <TableCell align="center">Name</TableCell>
                    <TableCell align="center">Price</TableCell>
                    <TableCell align="center">Shopping Site</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {order.products.map((product, idx) => (
                    <TableRow key={idx}>
                      <TableCell><img className={classes.productImage} src={product.photo} alt="logo" /></TableCell>
                      <TableCell align="left"><Link href={product.url} target='_blank'>{product.name}</Link></TableCell>
                      <TableCell align="center">$ {product.price}</TableCell>
                      <TableCell align="center"><Link href={product.domain} target='_blank'>{product.domain}</Link></TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </Box>
          </Collapse>
        </TableCell>
      </TableRow>
    </>
  );
}

const Orders = () => {
  const classes = useStyles();
  const dispatch = useDispatch();

  const [ orders, setOrders ] = useState([]);
  const [countPerPage] = useState(10);
  const [totalCount, setTotalCount] = useState(1);
  const [page, setPage] = useState(1);

  const fetchOrders = useCallback(() => {
    dispatch(actions.setLoading(true));
    api.getOrders({ page, countPerPage }).then((data) => {
      dispatch(actions.setLoading(false));
      console.log(data)
      if (data.error) {
        // setError(data.error);
        console.log(data.error);
      } else {
        setOrders(data.orders);
        setTotalCount(Math.floor((data.total - 1) / countPerPage) + 1);
      }
    });
  }, [page, countPerPage, dispatch])

  useEffect(() => {
    fetchOrders();
  }, [page, fetchOrders])

  const handlePageChange = (event, value) => {
    setPage(value);
  };

  return (
    <Layout>
      <Box marginY={1}>
        <Typography variant='h4'>All Orders</Typography>
      </Box>
      <TableContainer component={Paper} className={classes.tableBox}>
        <Table stickyHeader className={classes.table} aria-label="simple table">
          <TableHead>
            <TableRow>
              <TableCell width={80}></TableCell>
              <TableCell align="center">User</TableCell>
              <TableCell align="center">Shipping Cost</TableCell>
              <TableCell align="center">Stripe Fee</TableCell>
              <TableCell align="center">Total Price</TableCell>
              <TableCell align="center">Items</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {orders.map((order, idx) => (
              <OrderRow key={idx} order={order} />
            ))}
          </TableBody>
        </Table>
      </TableContainer>
      <Box className={classes.paginationWrapper}>
        <Pagination count={totalCount} page={page} onChange={handlePageChange} color="primary" showFirstButton showLastButton />
      </Box>
    </Layout>
  )
}

export default Orders;